.program{
    width: 100%;
        height: 100%;
        margin-left: auto;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
}
.program h2,h3{
    margin: 10px 0;
    padding: 0;
    text-align: left;
}
.programDetails{
    margin-top: 10px;
    
    padding: 10px;
}
.coverImage h2{
    text-align: center;
}
.coverImage span{
    text-align: center;
}

.program .coverImage{
    width: 100%;
    box-sizing: border-box;
    background: hsla(280, 84%, 41%, 1);

background: linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

background: -moz-linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

background: -webkit-linear-gradient(90deg, hsla(280, 84%, 41%, 1) 0%, hsla(218, 97%, 56%, 1) 100%);

filter: progid: DXImageTransform.Microsoft.gradient( startColorstr="#8711C1", endColorstr="#2472FC", GradientType=1 );
    padding: 50px 5px;
    position: relative;
    color: #f5f5f5;

}
.programDetails p{
    text-align: justify;
}
.programDetails ul{
    text-align: left;
}
button.joinGroup{
    padding: 5px;
    background-color: #f5f5f5;
    color: #005B7C;
    border: none;
    border-radius: 25px;
    right: 10px;
    top: 10px;
}

.save{
    padding: 5px;
    background-color: #f5f5f5;
    color: #005B7C;
    border: none;
    border-radius: 25px;
    right: 10px;
    top: 10px;
    margin-right: 10px;
}
.btn-grp{
    position: absolute;
    top: 10px;
    right: 10px;
}


@media screen and (min-width: 600px){
