.loading{
    width: 100%;
    min-height: 70%;
    margin: auto;
    box-sizing: border-box;
    background-color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;

}
.loading div{
    width: 50px;
    height: 50px;
    background: url(../../assets/loading.gif);
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
}

@media screen and (min-width: 600px){
    .loading{
        width: 100%;
        min-height: 80%;
        margin: auto;
        box-sizing: border-box;
        background-color: #ffffff;
        display: flex;
        justify-content: center;
        align-items: center;
    
    }
    .loading div{
        width: 50px;
        height: 50px;
        background: url(../../assets/loading.gif);
        background-repeat: no-repeat;
        background-size: 100%;
        background-position: center;
    }
}